@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@100;200;300;400;500;600;700&family=Limelight&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: 'Josefin Sans', sans-serif;
  background-color: #FFFDF4;
  line-height: 1.3;
}

html {
  scroll-behavior: smooth;
}


h1 {
  font-family: 'Limelight', cursive;
  font-size: 64px;
  font-weight: normal;
}

h2 {
  font-size: 42px;
}

h1, h2, h3, p {
  margin: 0;
}

ul {
  margin: 0;
  margin-right: 70px;
  margin-left: 27px;
  padding: 0;
  line-height: 1;
}

li {
  font-size: 30px;
}
li span {
  font-size: 20px;
}

.text-orange {
  color: #DF7861;
}

.text-sm {
  font-size: 20px;
}

.text-md {
  font-size: 28px;
}

.text-lg {
  font-size: 35px;
}

.text-300 {
  font-weight: 300;
}

.text-700 {
  font-weight: 700;
}

.text-20px {
  font-size: 20px;
}

.list-bullet {
  vertical-align: middle;
}

a {
  text-decoration: none;
}


.app {
  width: 90%;
  margin: 0 auto;
}

.navbar {
  background-color: #FFFDF4;
  padding: 10px 5%;
}

.navbar-brand {
  font-size: 54px !important;
}

.navbar-brand:hover {
  opacity: 0.8;
}

.navbar-link {
  color: black;
  margin-left: 30px;
  font-size: 24px !important;
  font-weight: 700;
}

.navbar-collapse {
  text-align: center;
}

.navbar-light .navbar-nav .nav-link {
  color: black;
}
#intro {
  max-width: 700px;
  margin: 0 auto;
  margin-top: 150px;
  margin-bottom: 200px;
}

.tech-stack {
  display: -webkit-flex;
  display: flex;
}

.intro a {
  color: #df7861;
  font-weight: 400;
}

@media screen and (max-width: 500px) {
  #intro {
    margin-top: 70px;
  }
  .tech-stack {
    display: inline;
    display: initial;
  }
}

.project-container {
  width: 1000px;
  margin: 100px auto;
  margin-bottom: 50px;
}

.project-item {
  width: 80%;
  height: 360px;
  border-radius: 34px 34px 150px 34px;
  padding: 30px;
  position: relative;
}

.bg-green {
  background-color: #E1F0E1;
}

.bg-blue {
  background-color: #D9EBED;
}

.project-info {
  width: 70%;
  padding-right: 48px;
}

/* IMAGE */

.image {
  background-position: center;
  background-size: cover;
  width: 95%;
  height: 300px;
  border-radius: 20px;
  position: absolute;
  top: -97px;
  right: -470px;
}

.image-container {
  width: 500px;
  position: relative;
}

/* LINKS  */

.links  {
  position: absolute;
  top: 338px;
  right: -209px;
}

.links p {
  margin-right: 10px;
  font-size: 28px;
  transition: 0.2s ease-in;
}

.links p:hover {
  color: black;
}

/* TAGS */

.tech-tags {
  margin-top: 16px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.tech-tag {
  color: white;
  background-color: #DF7861;
  padding: 5px 15px;
  border-radius: 20px;
  font-weight: 400;
  margin: 10px 10px 0 0;
}


/* RESPONSIVENESS */

@media screen and (max-width: 1100px) {
  .project-container {
    width: 100%;
  }
  .project-item {
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  }
  .image {
    position: static;
    position: initial;
    width: 100%;
    height: 0;
    padding-top: 63%;
  }
  .project-info {
    width: 100%;
    padding-right: 0;
  }
  .image-container {
    width: 100%;
  }
  .links  {
    top: 30px;
    right: 22px;
  }
  .tech-tags {
    width: 80%;
  }
  .project-text {
    margin-top: 20px;
  }
}

@media screen and (max-width: 450px) {
  .project-item {
    padding: 22px;
  }
  .links  {
    top: 17px;
    right: 16px;
  }
}

#projects {
  margin: 0 auto;
  margin-top: 80px;
  margin-bottom: 200px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

#contact {
  max-width: 700px;
  margin: 0 auto;
  margin-top: 150px;
  margin-bottom: 200px;
}

#contact a {
  transition: 0.2s ease-in;
  color: #df7861;
  font-weight: 400;
  text-decoration: none;
}

#contact a:hover {
  color: black;
}

.footer {
  text-align: center;
}

.social-links {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.social-links p {
  font-size: 46px;
  margin: 0 10px;
  transition: 0.2s ease-in;
}

.social-links p:hover {
  color: black;
}
